.card {
  position: relative;
  display: flex;
  justify-content: center;
  transition-duration: 0.5s;

  a {
    .cover-location {
      width: 340px;
      border-radius: 10px;
      object-fit: cover;
      height: 340px;
    }
    .name-location {
      padding: 0;
      margin: 0 20px 20px 20px;
      position: absolute;
      bottom: 40px;
      font-size: 18px;
      color: white;
      max-width: 340px;
    }
  }
}

.card:hover {
  transform: scale(105%);
}

@media (max-width: $brk_laptop_medium) {
  .card {
    a {
      .cover-location {
        width: 300px;
        height: 300px;
      }
    }
  }
}

@media (max-width: $brk_tablet_large_previous) {
  .card {
    a {
      .cover-location {
        width: 400px;
        height: 400px;
      }
    }
  }
}

@media (max-width: $brk_tablet_medium) {
  .card {
    a {
      .cover-location {
        width: 350px;
        height: 350px;
      }
    }
  }
}

@media (max-width: $brk_mobile_medium) {
  .card {
    a {
      .cover-location {
        width: 250px;
        height: 250px;
      }

      .name-location {
        font-size: 16px;
      }
    }
  }
}
