#lodgings-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 1240px;
  padding: 20px;
  justify-content: center;
  background-color: #f6f6f6;
  border-radius: 25px;
  margin: 0px auto 43px auto;

  .card {
    padding: 30px;
    background-color: #f6f6f6;
  }
}

@media (max-width: $brk_laptop_medium) {
  #lodgings-list {
    margin-left: 50px;
    margin-right: 50px;
    max-width: none;

    .card {
      padding: 10px;
    }
  }
}

@media (max-width: $brk_tablet_medium) {
  #lodgings-list {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (max-width: $brk_mobile_large) {
  #lodgings-list {
    background-color: white;

    .card {
      background-color: white;
    }
  }
}
